"use client";

import { useState, useEffect, useRef } from "react";
import { createClient } from "@supabase/supabase-js";
import GlobeView from "@/components/GlobeView";

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
);

interface Country {
  id: number;
  name: string;
  region: string;
  risk_score: number;
}

export default function HomePage() {
  const [countries, setCountries] = useState<Country[]>([]);
  const [loading, setLoading] = useState(true);
  const [regionPing, setRegionPing] = useState<string>("");
  const [pulseCountry, setPulseCountry] = useState<number | null>(null);
  const [beamCountry, setBeamCountry] = useState<number | null>(null);
  const [volume, setVolume] = useState(0.5);
  const [audioEnabled, setAudioEnabled] = useState(true);
  const audioCtx = useRef<AudioContext | null>(null);

  // === AUDIO ===
  function playTone(frequency: number, duration = 0.3) {
    if (!audioEnabled) return;
    if (!audioCtx.current) audioCtx.current = new AudioContext();
    const ctx = audioCtx.current;
    const osc = ctx.createOscillator();
    const gain = ctx.createGain();
    osc.frequency.value = frequency;
    gain.gain.value = volume;
    osc.connect(gain);
    gain.connect(ctx.destination);
    osc.start();
    osc.stop(ctx.currentTime + duration);
  }

  // === LOAD INITIAL DATA ===
  async function loadData() {
    const { data, error } = await supabase.from("countries").select("*");
    if (error) console.error("‚ùå Error loading countries:", error);
    if (data) {
      data.sort((a, b) => b.risk_score - a.risk_score);
      setCountries(data);
    }
    setLoading(false);
  }

  useEffect(() => {
    loadData();
  }, []);

  // === REALTIME UPDATES ===
  useEffect(() => {
    const channel = supabase
      .channel("table-changes")
      .on(
        "postgres_changes",
        { event: "UPDATE", schema: "public", table: "countries" },
        (payload) => {
          const updated = payload.new as Country;
          setCountries((prev) => {
            const newList = prev.map((c) =>
              c.id === updated.id ? updated : c
            );
            newList.sort((a, b) => b.risk_score - a.risk_score);
            return [...newList];
          });
          triggerEffects(updated);
        }
      )
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, []);

  // === MASTER VISUAL + AUDIO EFFECTS ===
  function triggerEffects(country: Country) {
    setRegionPing(country.region);
    setPulseCountry(country.id);
    setBeamCountry(country.id);
    playTone(200 + country.risk_score * 3);
    setTimeout(() => {
      setPulseCountry(null);
      setBeamCountry(null);
    }, 1600);
  }

  // === COLOR UTIL ===
  function riskColor(score: number) {
    if (score >= 80) return "bg-red-500 text-white";
    if (score >= 60) return "bg-amber-400 text-slate-900";
    return "bg-blue-400 text-white";
  }

  return (
    <main className="min-h-screen bg-slate-950 text-slate-100 relative overflow-hidden">
      {/* COMMAND CENTER BACKGROUND */}
      <div className="absolute inset-0 bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900" />
      <div className="absolute inset-0 opacity-[0.15] bg-[radial-gradient(circle_at_center,rgba(59,130,246,0.3)_0%,transparent_60%)] animate-[pulse_10s_ease-in-out_infinite]" />
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_center,rgba(250,204,21,0.15)_0%,transparent_70%)] blur-2xl" />

      {/* MAIN GRID */}
      <div className="relative z-10 max-w-7xl mx-auto px-6 py-8 grid md:grid-cols-[280px_1fr_300px] gap-6">

        {/* CRASH WATCH LIST */}
        <aside className="rounded-2xl border border-slate-700 bg-slate-900/60 p-4 backdrop-blur-sm">
          <h2 className="text-lg font-semibold mb-3 text-amber-300">üö® Crash Watch</h2>
          {loading ? (
            <p className="text-slate-400 text-sm">Loading data...</p>
          ) : (
            <div className="space-y-2 max-h-[520px] overflow-y-auto relative">
              {countries.map((c, index) => (
                <div
                  key={c.id}
                  className={`flex items-center justify-between px-3 py-2 rounded-lg cursor-pointer transition-all duration-500 relative ${
                    pulseCountry === c.id
                      ? "bg-red-500/30 shadow-[0_0_10px_rgba(239,68,68,0.6)] scale-[1.03]"
                      : "hover:bg-slate-800/60"
                  }`}
                  onClick={() => triggerEffects(c)}
                >
                  <span className="text-sm font-medium">{c.name}</span>
                  <span
                    className={`text-xs px-2 py-1 rounded-md font-semibold ${riskColor(
                      c.risk_score
                    )}`}
                  >
                    {c.risk_score}
                  </span>

                  {/* üåê DATA BEAM from globe to list */}
                  {beamCountry === c.id && (
                    <div className="absolute -left-10 top-1/2 h-[2px] w-[calc(100%+40px)] bg-gradient-to-r from-sky-400 via-amber-300 to-transparent animate-signal" />
                  )}
                </div>
              ))}
            </div>
          )}
        </aside>

        {/* GLOBE CENTER */}
        <section className="relative flex flex-col items-center justify-center rounded-2xl border border-slate-700 bg-slate-900/60 p-6 overflow-hidden">
          <h2 className="text-lg font-semibold mb-2 text-slate-200">üåç Global Monitor</h2>
          <div className="relative">
            <GlobeView width={280} height={240} regionPing={regionPing} />
            <div className="absolute inset-0 animate-[wave_4s_ease-in-out_infinite] bg-[radial-gradient(circle_at_center,rgba(59,130,246,0.25)_0%,transparent_70%)] blur-3xl rounded-full" />
          </div>

          {/* Beam Source Glow */}
          {beamCountry && (
            <div className="absolute left-[60%] top-[50%] w-[200%] h-[3px] bg-gradient-to-r from-amber-400/80 via-blue-400/60 to-transparent rounded-full animate-wavebeam" />
          )}
        </section>

        {/* INTELLIGENCE LOG */}
        <aside className="rounded-2xl border border-slate-700 bg-slate-900/60 p-4 backdrop-blur-sm">
          <h2 className="text-lg font-semibold mb-3 text-amber-300">üß† Command Log</h2>
          <ul className="space-y-2 text-sm text-slate-300 max-h-[520px] overflow-y-auto">
            {countries.slice(0, 6).map((c) => (
              <li key={c.id} className="flex justify-between">
                <span>{c.name}</span>
                <span className={riskColor(c.risk_score)}>{c.risk_score}</span>
              </li>
            ))}
          </ul>

          <div className="mt-6 border-t border-slate-700 pt-4">
            <label className="text-xs text-slate-400 block mb-1">Volume</label>
            <input
              type="range"
              min="0"
              max="1"
              step="0.05"
              value={volume}
              onChange={(e) => setVolume(parseFloat(e.target.value))}
              className="w-full accent-amber-400"
            />
          </div>
        </aside>
      </div>

      {/* COMMAND CENTER ANIMATIONS */}
      <style jsx global>{`
        @keyframes wave {
          0% { opacity: 0.3; transform: scale(0.8); }
          50% { opacity: 0.6; transform: scale(1.1); }
          100% { opacity: 0.3; transform: scale(0.8); }
        }
        @keyframes pulse {
          0%, 100% { opacity: 0.2; }
          50% { opacity: 0.4; }
        }
        @keyframes signal {
          0% { opacity: 0; transform: scaleX(0); }
          30% { opacity: 1; transform: scaleX(1.05); }
          100% { opacity: 0; transform: scaleX(1.2); }
        }
        @keyframes wavebeam {
          0% { opacity: 0; transform: scaleX(0.5); }
          30% { opacity: 1; transform: scaleX(1.05); }
          100% { opacity: 0; transform: scaleX(1.4); }
        }
        .animate-signal { animation: signal 1.2s ease-out; }
        .animate-wavebeam { animation: wavebeam 1.3s ease-out; }
      `}</style>
    </main>
  );
}
